import * as React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@mui/material";
import { CurveFitData } from "../../common/selectors/DataSelectors";
import { computeModelPosition } from "./ComputeModelPosition";

export interface CurveFitTableProps {
  curveFit: CurveFitData;
  splits: Record<number, number | undefined>;
}

interface Row {
  actualPosition: number;
  modelPosition: number;
  sqrDiff: number;
}

export function CurveFitTable(props: CurveFitTableProps): JSX.Element {
  const { curveFit, splits } = props;
  const { vmax, tau } = curveFit;

  const rows: Record<number, Row> = React.useMemo(() => {
    const result: Record<number, Row> = {};

    for (const [key, value] of Object.entries(splits)) {
      if (value === undefined) {
        continue;
      }

      const actualPosition = parseFloat(key);
      const modelPosition = computeModelPosition(
        curveFit.vmax,
        curveFit.tau,
        value
      );

      result[value] = {
        actualPosition: actualPosition,
        modelPosition: modelPosition,
        sqrDiff: Math.pow(modelPosition - actualPosition, 2),
      };
    }

    return result;
  }, [curveFit]);

  const sumSqrDiffs = React.useMemo(() => {
    let sum = 0;
    for (const row of Object.values(rows)) {
      sum += row.sqrDiff;
    }
    return sum;
  }, [rows]);

  return (
    <React.Fragment>
      Vmax: {vmax}
      <br />
      Tau: {tau}
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Time</TableCell>
            <TableCell>Actual Position</TableCell>
            <TableCell>Position model</TableCell>
            <TableCell>Square Difference</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {Object.entries(rows).map(([time, row]) => (
            <TableRow key={time}>
              <TableCell>{time}</TableCell>
              <TableCell>{row.actualPosition.toFixed(1)}</TableCell>
              <TableCell>{row.modelPosition.toFixed(1)}</TableCell>
              <TableCell>{row.sqrDiff.toFixed(1)} </TableCell>
            </TableRow>
          ))}
          <TableRow>
            <TableCell />
            <TableCell />
            <TableCell />
            <TableCell>
              Sum
              <br />
              {sumSqrDiffs.toFixed(3)}
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </React.Fragment>
  );
}
