import { createSelector } from "@reduxjs/toolkit";
import { useSelector } from "react-redux";
import { RootState } from "../../app/ReduxProvider";
import { InputData } from "../../features/input-data/InputDataSlice";
import { isDataComplete } from "../utilities/isDataComplete";
import { SplitsData } from "../../features/splits/SplitsSlice";
import { levenbergMarquardt } from "ml-levenberg-marquardt";
import { computeModelPosition } from "../../features/curve-fit/ComputeModelPosition";
import { exponential} from 'regression';
import { CurveFitVars } from "../../features/curve-fit/CurveFitSlice";

type LevenbergMarquardtData = { x: Array<number>; y: Array<number> };
type LevenbergMarquardtOutput = {
  parameterValues: Array<number>;
  parameterError: number;
  iterations: number;
};

export interface CurveFitData {
  vmax: number;
  tau: number;
  error: number;
}

export interface SprintData {
  time: number;
  position: number;
  velocity: number;
  hztAccel: number;
  airFriction: number;
  hztNetForce: number;
  hztNetForcePerWeight: number;
  hztNetPower: number;
  hztNetPowerPerWeight: number;
  vtcForce: number | undefined;
  ratioOfForce: number | undefined;
}

const INTERVAL_INCREMENT = 0.01;

function getInputData(state: RootState): Partial<InputData> {
  return state.inputData;
}

function getSplitsData(state: RootState): SplitsData {
  return state.splits;
}

function getCurveFitVars(state: RootState): CurveFitVars{
  return state.curveFit;
}

function computePositionalFunctionFactory([vmax, tau]: [number, number]): (
  x: number
) => number {
  return (time) => computeModelPosition(vmax, tau, time);
}

export const selectFitVariables = createSelector(
  getInputData,
  getSplitsData,
  getCurveFitVars,
  (
    inputData: Partial<InputData>,
    splits: SplitsData,
    curveFitVars: CurveFitVars,
  ): CurveFitData | undefined => {
    if (!isDataComplete(inputData)) {
      return undefined;
    }

    const data: LevenbergMarquardtData = { x: [], y: [] };

    for (const [key, value] of Object.entries(splits)) {
      const y = parseFloat(key);

      if (isNaN(y) || value === undefined) {
        continue;
      }

      data.x.push(value);
      data.y.push(y);
    }

    let initialValues = [7, 1];

    const fittedParams: LevenbergMarquardtOutput = levenbergMarquardt(
      data,
      computePositionalFunctionFactory,
      {
        initialValues: initialValues,
        ...curveFitVars,
      }
    );

    return {
      vmax: fittedParams.parameterValues[0],
      tau: fittedParams.parameterValues[1],
      error: fittedParams.parameterError,
    };
  }
);

const selectVelocityData = createSelector(
  getInputData,
  selectFitVariables,
  (
    inputData: Partial<InputData>,
    fitVariables: CurveFitData | undefined
  ): SprintData[] => {
    if (!isDataComplete(inputData) || fitVariables === undefined) {
      return [];
    }

    const { vmax, tau } = fitVariables;

    let position = 0;
    const result = [];

    for (
      let interval = INTERVAL_INCREMENT;
      interval <= 6;
      interval += INTERVAL_INCREMENT
    ) {
      const velocity = vmax * (1 - Math.exp(-interval / tau));

      position += velocity * INTERVAL_INCREMENT;

      const hztAccel = (vmax / tau) * Math.exp((-1 * interval) / tau);

      const airFriction =
        0.5 *
        (1.293 *
          (((inputData.pressure / 760) * 273) /
            (273 + inputData.temperature))) *
        (0.2025 *
          Math.pow(inputData.stature, 0.725) *
          Math.pow(inputData.mass, 0.425) *
          0.266) *
        0.9 *
        Math.pow(velocity, 2);

      const hztNetForce = airFriction + inputData.mass * hztAccel;

      const hztNetPower = hztNetForce * velocity;

      let vtcForce;
      let ratioOfForce;

      if (interval >= 0.5) {
        vtcForce = inputData.mass * 9.81;
        ratioOfForce =
          hztNetForce /
          Math.sqrt(Math.pow(hztNetForce, 2) + Math.pow(vtcForce, 2));
      }

      result.push({
        position: position,
        velocity: velocity,
        time: interval,
        hztAccel: hztAccel,
        airFriction: airFriction,
        hztNetForce: hztNetForce,
        hztNetForcePerWeight: hztNetForce / inputData.mass,
        hztNetPower: hztNetPower,
        hztNetPowerPerWeight: hztNetPower / inputData.mass,
        ratioOfForce: ratioOfForce,
        vtcForce: vtcForce,
      });
    }

    return result;
  }
);

export function useInputData(): Partial<InputData> {
  return useSelector(getInputData);
}

export function useFitData(): CurveFitData | undefined {
  return useSelector(selectFitVariables);
}

export function useFullDataSet(): SprintData[] {
  return useSelector(selectVelocityData);
}

export function useSplits(): SplitsData {
  return useSelector(getSplitsData);
}

export function useCurveFitVars(): CurveFitVars{
  return useSelector(getCurveFitVars);
}