import { Data } from "plotly.js";
import React from "react";
import Plot from "react-plotly.js";
import { useFullDataSet } from "../../common/selectors/DataSelectors";

export function Plots(): JSX.Element {
  const dataSet = useFullDataSet();

  const positionVsTimeData = React.useMemo<Data[]>(() => {
    return [
      {
        x: dataSet.map((d) => d.time),
        y: dataSet.map((d) => d.position),
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "blue" },
      },
    ];
  }, [dataSet]);

  const velocityVsTimeData = React.useMemo<Data[]>(() => {
    return [
      {
        x: dataSet.map((d) => d.time),
        y: dataSet.map((d) => d.velocity),
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "blue" },
      },
    ];
  }, [dataSet]);

  const hztForceVsTimeData = React.useMemo<Data[]>(() => {
    return [
      {
        x: dataSet.map((d) => d.time),
        y: dataSet.map((d) => d.hztNetForcePerWeight),
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "blue" },
      },
    ];
  }, [dataSet]);

  const hztPowerPerTime = React.useMemo<Data[]>(() => {
    return [
      {
        x: dataSet.map((d) => d.time),
        y: dataSet.map((d) => d.hztNetPowerPerWeight),
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "blue" },
      },
    ];
  }, [dataSet]);

  const forceVelocityPowerData = React.useMemo<Data[]>(() => {
    return [
      {
        x: dataSet.map((d, i) => i),
        y: dataSet.map((d) => d.hztNetForcePerWeight),
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "Orange" },
      },
    ];
  }, [dataSet]);

  const ratioOfForceData = React.useMemo<Data[]>(() => {
    const xValues: number[] = [];
    const yValues: number[] = [];

    for (let i = 0; i < dataSet.length; i++) {
      const d = dataSet[i];
      if (d.velocity === undefined || d.ratioOfForce === undefined) {
        continue;
      }

      xValues.push(i);
      yValues.push(d.ratioOfForce);
    }

    return [
      {
        x: xValues,
        y: yValues,
        type: "scatter",
        mode: "lines+markers",
        marker: { color: "Orange" },
      },
    ];
  }, [dataSet]);

  return (
    <React.Fragment>
      <Plot data={positionVsTimeData} layout={{ title: "Position(t)" }} />
      <Plot data={velocityVsTimeData} layout={{ title: "Velocity(t)" }} />
      <Plot data={hztForceVsTimeData} layout={{ title: "HZT Force(t)" }} />
      <Plot data={hztPowerPerTime} layout={{ title: "HZT Power(t)" }} />
      <Plot
        data={forceVelocityPowerData}
        layout={{
          title: "Force-Velocity-Power",
        }}
      />
      <Plot
        data={ratioOfForceData}
        layout={{
          title: "Ratio of Force - Velocity",
        }}
      />
    </React.Fragment>
  );
}
