import * as React from "react";
import { useDispatch } from "react-redux";
import { NumericInput } from "../../common/components/NumericInput";
import {
  useCurveFitVars,
  useFitData,
  useSplits,
} from "../../common/selectors/DataSelectors";
import { CURVE_FIT_SLICE } from "./CurveFitSlice";
import { CurveFitTable } from "./CurveFitTable";

export function CurveFitValues(): JSX.Element | null {
  const splits = useSplits();
  const curveFit = useFitData();
  const curveFitVars = useCurveFitVars();
  const dispatch = useDispatch();

  if (curveFit === undefined) {
    return <span>Please complete the input data section</span>;
  }

  return (
    <React.Fragment>
      <div>
      <NumericInput
        value={curveFitVars.damping}
        label="damping"
        onChange={(v) =>
          dispatch(CURVE_FIT_SLICE.actions.setCurveFitData({ damping: v }))
        }
      />
      </div>
      <CurveFitTable curveFit={curveFit} splits={splits} />
    </React.Fragment>
  );
}
