import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface CurveFitVars {
    damping: number,
    gradientDifference: number,
    maxIterations: number,
    errorTolerance: number,
}

const INITIAL_STATE = Object.freeze<CurveFitVars>({  
  damping: 0.5,
  gradientDifference: 10e-6,
  maxIterations: 500,
  errorTolerance: 10e-3,
});

export const CURVE_FIT_SLICE = createSlice({
  name: "curveFitVars",
  initialState: INITIAL_STATE,
  reducers: {
    setCurveFitData: (
      state: CurveFitVars,
      action: PayloadAction<Partial<CurveFitVars>>
    ) => {
      Object.assign(state, action.payload);
    },
  },
});
